

html {
  background-color: #282c34;
  margin: 0;
  padding: 0;
  color: white;
  font-size: 16px;
}

img {
  width: 20%;
  height: auto;
  max-width: 30%;
}

video {
  width: auto;
  height: auto;
  max-width: 30%;
  object-fit: cover;
}

#name {
  text-align: center;
  font-size: 5rem;
}

.media {
  display: flex;
  justify-content: center;
}

.info-holder {
  text-align: center;
}

#info {
  padding: 2rem;
  font-size: 2rem;
  text-align: center;
  visibility: hidden;
}

button {
  color: black;
  background-color: white;
  padding: 0.5rem;
  border-radius: 5px;
  border: 2px solid black;
  box-shadow: 5px 5px;
  margin-top: 2rem;
}

button:hover {
  cursor: pointer;
  background-color: whitesmoke;
  box-shadow: -5px 5px;
}

button:active + #info {
  visibility: visible;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
